import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import QvipleLoader from "../../../Loader/QvipleLoader";
const LmsSanctionAuthorityRequest = lazy(() =>
  import("./LmsSanctionAuthorityRequest")
);
const LmsSanctionAuthorityHistory = lazy(() =>
  import("./LmsSanctionAuthorityHistory")
);

const LmsSanctionAuthority = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  return (
    <>
      <Suspense fallback={<QvipleLoader />}>
        {searchParams.get("type") === "mod-request" ? (
          <LmsSanctionAuthorityRequest
            modId={getQuery?.state?.onClickValue?._id}
          />
        ) : searchParams.get("type") === "mod-history" ? (
          <LmsSanctionAuthorityHistory
            modId={getQuery?.state?.onClickValue?._id}
          />
        ) : null}
      </Suspense>
    </>
  );
};

export default LmsSanctionAuthority;
